
import { Vue, Options } from "vue-class-component";
import RepoItem from "@/components/RepoItem.vue";
import { useStore } from "@/store";
@Options({
  components: { RepoItem },
})
export default class ProjectView extends Vue {
  sort = "updated";
  direction = "desc";
  sortFilter = [
    { title: "Create", value: "created" },
    { title: "Update", value: "updated" },
    { title: "Push", value: "pushed" },
    { title: "Full Name", value: "full_name" },
  ];
  store = useStore();
  attrs = {
    class: "mb-6",
    boilerplate: true,
    elevation: 2,
  };
  isLoading = false;
  async fetch() {
    this.isLoading = true;
    this.store.state.repositories = await this.$apiServices.gerRepository(
      this.sort,
      this.direction
    );
    this.isLoading = false;
  }
  toggleSortDirection() {
    this.direction = this.direction == "asc" ? "desc" : "asc";
  }
  created() {
    this.$watch("sort", () => {
      this.fetch();
    });
    this.$watch("direction", () => {
      this.fetch();
    });
  }
  mounted() {
    console.log("Mounted");
    if (this.store.state.repositories.length == 0) {
      this.fetch();
    }
  }
}
